 /* eslint-disable */ 

<template>
 <div class="Poll">
        <span><h1>I am.....</h1></span>

 <div>
    <div>
   <i class="fa-local fa-female material-icons md-48" title="Female" style="font-size: 50px" ></i>
   <i class="fa fa-male" title="Male" style="font-size: 50px" ></i>
    </div>
    <div>
        <span>{{FemalePercent}}%</span>
        <span>{{MalePercent}}%</span>
    </div>
    <div>
     <md-button class="md-primary" @click.native="onFemaleClicked">Vote </md-button>
     <md-button class="md-primary" @click.native="onMaleClicked">Vote2</md-button>
    </div>
    <span>Female Vote: {{FemaleVote}}</span>
    <span>Male Vote: {{MaleVote}}</span>
 </div>

     <md-button class="md-primary" @click.native="OnReset" >Reset</md-button>
 <div>
 </div>
 </div>
</template>

<script>
export default {
  name: 'Poll',
  data: () => ({
    FemaleVote: 0,
    MaleVote: 0,
    FemalePercent:0,
    MalePercent:0
  }),
  computed: {
     CalculatePercentage () {
       let totalVotes = parseInt(this.FemaleVote) + parseInt(this.MaleVote);
          if(this.totalVotes === 0) { 
            this.FemalePercent = '0';
            this.MalePercent = '0';
          } else {
            this.FemalePercent = ( Math.round((this.FemaleVote * 100)/totalVotes ));
            this.MalePercent = ( Math.round((this.MaleVote * 100)/ totalVotes ) );
          }
         
     }
  },
  methods: {
     onFemaleClicked (){
       if(this.FemaleVote === undefined || this.FemaleVote === null){
         this.FemaleVote = 0;
       } else {
       this.FemaleVote++;
       }
       this.CalculatePercentage;
     },
     onMaleClicked (){
       console.log(this.MaleVote);
       if(this.MaleVote === undefined || this.MaleVote === null){
         this.MaleVote = 1;
       } else {
       this.MaleVote++;
       }
       this.CalculatePercentage;
     },
     OnReset () {
        this.FemaleVote = 0;
        this.MaleVote = 0;
        this.FemalePercent = 0;
        this.MalePercent = 0;
     }
  }
}
</script>

<style lang="scss" scoped>
  small {
    display: block;
  }

  .md-theme-default.md-radio:not([disabled]).md-fab {
    color: rgba(255, 255, 255, .87);
    background-color: #90EE90;
}

.fa-local {
    display: inline-block;
    font: normal normal normal 48px/1 FontAwesome;
    font-size: inherit;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

</style>



