<template>
  <div id="app">
     <!--<md-toolbar class="app-toolbar">
      <md-button class="md-icon-button" @click.native="toggleLeftSidenav">
        <md-icon>menu</md-icon>
      </md-button>
      <h2 class="md-title">Poll</h2>
      <span style="flex: 1"></span>
    </md-toolbar>
    -->

    <md-sidenav class="md-left" ref="leftSidenav">
          <!--<md-toolbar class="md-large">
            <div class="md-toolbar-container">
              <div>
                <span class="md-headline">
                  <md-icon>account_circle</md-icon>
                  {{ loggedInUser.fullName }}
                </span>
                <div class="md-subheading">{{ loggedInUser.userName }}</div>
              </div>
            </div>
          </md-toolbar> -->

          <md-list v-on:click.native="onSideNavMenuItemClicked">
            <md-list-item class="md-primary">
              <router-link :to="{ name: 'CanvasSquare' }">Canvas</router-link>
            </md-list-item>
            <md-list-item class="md-primary">
            <router-link :to="{ name: 'BlueSquare' }">Square</router-link>
            </md-list-item>
             <md-list-item class="md-primary">
            <router-link :to="{ name: 'GenderPoll' }">Gender Poll</router-link>
            </md-list-item>
          </md-list>
        </md-sidenav>

    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
   methods: {
    toggleLeftSidenav () {
      this.$refs.leftSidenav.toggle()
    },
     onSideNavMenuItemClicked (e) {
      this.$refs.leftSidenav.toggle()
      if (!e.target.dataset || !e.target.dataset.to) {
        return
      }
      this.$router.replace(e.target.dataset.to)
    },
   }
};
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



